import React, { createContext, useContext, useReducer, useEffect, useCallback } from 'react';
import axios from 'axios';
import { User, AuthState } from '../types';

// Define action types
type AuthAction =
    | { type: 'LOGIN_SUCCESS'; payload: { token: string; user: User } }
    | { type: 'REGISTER_SUCCESS'; payload: { token: string; user: User } }
    | { type: 'LOGOUT' }
    | { type: 'AUTH_ERROR'; payload: string }
    | { type: 'CLEAR_ERROR' }
    | { type: 'LOADING' }
    | { type: 'USER_LOADED'; payload: User };

// Initial state
const initialState: AuthState = {
    isAuthenticated: false,
    user: JSON.parse(localStorage.getItem('user') || 'null'),
    loading: true,
    error: null
};

// Create context
interface AuthContextType {
    state: AuthState;
    login: (email: string, password: string) => Promise<void>;
    register: (username: string, email: string, password: string) => Promise<void>;
    logout: () => void;
    clearError: () => void;
    updateProfile: (userData: Partial<User>) => Promise<void>;
}

const defaultContextValue: AuthContextType = {
    state: initialState,
    login: async () => { throw new Error('AuthContext not initialized'); },
    register: async () => { throw new Error('AuthContext not initialized'); },
    logout: () => { throw new Error('AuthContext not initialized'); },
    clearError: () => { throw new Error('AuthContext not initialized'); },
    updateProfile: async () => { throw new Error('AuthContext not initialized'); }
};

const AuthContext = createContext<AuthContextType>(defaultContextValue);

// Reducer function
const authReducer = (state: AuthState, action: AuthAction): AuthState => {
    switch (action.type) {
        case 'USER_LOADED':
            return {
                ...state,
                isAuthenticated: true,
                user: action.payload,
                loading: false
            };
        case 'LOGIN_SUCCESS':
        case 'REGISTER_SUCCESS':
            localStorage.setItem('token', action.payload.token);
            localStorage.setItem('user', JSON.stringify(action.payload.user));
            return {
                ...state,
                isAuthenticated: true,
                user: action.payload.user,
                loading: false,
                error: null
            };
        case 'LOGOUT':
        case 'AUTH_ERROR':
            localStorage.removeItem('token');
            localStorage.removeItem('user');
            return {
                ...state,
                isAuthenticated: false,
                user: null,
                loading: false,
                error: action.type === 'AUTH_ERROR' ? action.payload : null
            };
        case 'LOADING':
            return {
                ...state,
                loading: true
            };
        case 'CLEAR_ERROR':
            return {
                ...state,
                error: null
            };
        default:
            return state;
    }
};

// Provider component
export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [state, dispatch] = useReducer(authReducer, initialState);

    const setAuthToken = (token: string | null) => {
        if (token) {
            axios.defaults.headers.common['Authorization'] = `Bearer ${token}`;
        } else {
            delete axios.defaults.headers.common['Authorization'];
        }
    };

    const loadUser = async () => {
        const token = localStorage.getItem('token');
        const user = localStorage.getItem('user');

        if (token && user) {
            setAuthToken(token);
            dispatch({ type: 'USER_LOADED', payload: JSON.parse(user) });
        } else {
            dispatch({ type: 'AUTH_ERROR', payload: '' });
        }
    };

    const login = async (email: string, password: string) => {
        try {
            dispatch({ type: 'LOADING' });
            const res = await axios.post('/api/auth/login', { email, password });
            dispatch({ type: 'LOGIN_SUCCESS', payload: res.data });
        } catch (err: any) {
            const errorMessage = err.response?.data?.message || 'Login failed. Please try again.';
            dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
            throw new Error(errorMessage);
        }
    };

    const register = async (username: string, email: string, password: string) => {
        try {
            dispatch({ type: 'LOADING' });
            const res = await axios.post('/api/auth/register', { username, email, password });
            dispatch({ type: 'REGISTER_SUCCESS', payload: res.data });
        } catch (err: any) {
            const errorMessage = err.response?.data?.message || 'Registration failed. Please try again.';
            dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
            throw new Error(errorMessage);
        }
    };

    const logout = () => {
        dispatch({ type: 'LOGOUT' });
    };

    const clearError = useCallback(() => {
        dispatch({ type: 'CLEAR_ERROR' });
    }, []);

    const updateProfile = async (userData: Partial<User>) => {
        try {
            dispatch({ type: 'LOADING' });
            const res = await axios.put('/api/users/profile', userData);
            dispatch({ type: 'USER_LOADED', payload: res.data });
        } catch (err: any) {
            const errorMessage = err.response?.data?.message || 'Profile update failed. Please try again.';
            dispatch({ type: 'AUTH_ERROR', payload: errorMessage });
            throw new Error(errorMessage);
        }
    };

    useEffect(() => {
        loadUser();
    }, []);

    return (
        <AuthContext.Provider value={{ state, login, register, logout, clearError, updateProfile }}>
            {children}
        </AuthContext.Provider>
    );
};

export const useAuth = () => {
    const context = useContext(AuthContext);
    if (context === defaultContextValue) {
        throw new Error('useAuth must be used within an AuthProvider. Make sure your component is wrapped with AuthProvider in the component tree.');
    }
    return context;
};
