/**
 * Get movie details by ID
 * @param id - Movie ID
 * @returns Promise with movie details
 */
export const getMovieDetails = async (id: string): Promise<Movie> => {
    try {
        // For development, return mock data instead of making API call
        // Convert string ID to number for comparison with mock data
        const numericId = parseInt(id, 10);
        const movie = mockMovies.find(m => m.id === numericId);
        if (movie) {
            return movie;
        }
        return mockMovies[0]; // Return first movie as fallback

        // Uncomment this when the server is running
        // const response = await axios.get(`${API_URL}/${id}`);
        // return response.data;
    } catch (error) {
        console.error(`Error fetching movie details for ID ${id}:`, error);
        throw error;
    }
};

/**
 * Search movies by query
 * @param query - Search query
 * @param page - Page number for pagination
 * @returns Promise with search results
 */
export const searchMovies = async (query: string, page: number = 1): Promise<{ results: Movie[], total_pages: number }> => {
    try {
        // For development, return mock data instead of making API call
        const filteredMovies = mockMovies.filter(movie =>
            movie.title.toLowerCase().includes(query.toLowerCase()) ||
            movie.overview.toLowerCase().includes(query.toLowerCase())
        );

        return {
            results: filteredMovies,
            total_pages: 1
        };

        // Uncomment this when the server is running
        // const response = await axios.get(`${API_URL}/search?query=${encodeURIComponent(query)}&page=${page}`);
        // return response.data;
    } catch (error) {
        console.error('Error searching movies:', error);
        throw error;
    }
};

/**
 * Get movies by genre
 * @param genreId - Genre ID
 * @param page - Page number for pagination
 * @returns Promise with movies in the specified genre
 */
export const getMoviesByGenre = async (genreId: number, page: number = 1): Promise<{ results: Movie[], total_pages: number }> => {
    try {
        // For development, return mock data instead of making API call
        const filteredMovies = mockMovies.filter(movie =>
            movie.genres && movie.genres.some(genre => genre.id === genreId)
        );

        return {
            results: filteredMovies,
            total_pages: 1
        };

        // Uncomment this when the server is running
        // const response = await axios.get(`${API_URL}/genre/${genreId}?page=${page}`);
        // return response.data;
    } catch (error) {
        console.error(`Error fetching movies for genre ID ${genreId}:`, error);
        throw error;
    }
};

