import React, { useState, useEffect } from 'react';
import {
    Container,
    Typography,
    Box,
    Grid,
    CircularProgress,
    Pagination,
    FormControl,
    InputLabel,
    Select,
    MenuItem,
    OutlinedInput,
    Checkbox,
    ListItemText,
    useTheme,
    alpha,
    SelectChangeEvent
} from '@mui/material';
import { Movie, Genre } from '../types';
import { getTrendingMovies, getMovieGenres } from '../services/movieService';
import MovieGrid from '../components/Movies/MovieGrid';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
    PaperProps: {
        style: {
            maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
            width: 250,
        },
    },
};

const MoviesPage: React.FC = () => {
    const theme = useTheme();
    const [movies, setMovies] = useState<Movie[]>([]);
    const [genres, setGenres] = useState<Genre[]>([]);
    const [selectedGenres, setSelectedGenres] = useState<number[]>([]);
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);
    const [page, setPage] = useState<number>(1);
    const [totalPages, setTotalPages] = useState<number>(1);
    const [genreMap, setGenreMap] = useState<Record<number, string>>({});

    useEffect(() => {
        const fetchData = async () => {
            try {
                setLoading(true);

                // Fetch genres
                const genresData = await getMovieGenres();
                setGenres(genresData);

                // Create genre map
                const genreMapping: Record<number, string> = {};
                genresData.forEach((genre: Genre) => {
                    genreMapping[genre.id] = genre.name;
                });
                setGenreMap(genreMapping);

                // Fetch movies
                const moviesData = await getTrendingMovies(page);
                setMovies(moviesData.results || []);
                setTotalPages(moviesData.total_pages || 1);

                setLoading(false);
            } catch (error) {
                console.error('Error fetching data:', error);
                setError('Failed to fetch movies. Please try again later.');
                setLoading(false);
            }
        };

        fetchData();
    }, [page]);

    // Filter movies by selected genres
    const filteredMovies = selectedGenres.length > 0
        ? movies.filter(movie =>
            movie.genres?.some(genre => selectedGenres.includes(genre.id)) ||
            (movie as any).genre_ids?.some((id: number) => selectedGenres.includes(id))
        )
        : movies;

    const handleGenreChange = (event: SelectChangeEvent<number[]>) => {
        const value = event.target.value as number[];
        setSelectedGenres(value);
    };

    const handlePageChange = (event: React.ChangeEvent<unknown>, value: number) => {
        setPage(value);
        window.scrollTo({ top: 0, behavior: 'smooth' });
    };

    if (error) {
        return (
            <Box sx={{ textAlign: 'center', my: 8 }}>
                <Typography variant="h5" color="error" gutterBottom>
                    {error}
                </Typography>
            </Box>
        );
    }

    return (
        <Container maxWidth="xl" sx={{ py: 4 }}>
            <Typography
                variant="h3"
                component="h1"
                fontWeight="bold"
                sx={{ mb: 4 }}
            >
                Movies
            </Typography>

            {/* Genre filter dropdown */}
            <Box sx={{ mb: 4, maxWidth: 400 }}>
                <FormControl fullWidth>
                    <InputLabel id="genre-filter-label">Filter by Genre</InputLabel>
                    <Select
                        labelId="genre-filter-label"
                        id="genre-filter"
                        multiple
                        value={selectedGenres}
                        onChange={handleGenreChange}
                        input={<OutlinedInput label="Filter by Genre" />}
                        renderValue={(selected) => (
                            selected.length === 0
                                ? 'All Genres'
                                : selected.map(id => genreMap[id]).join(', ')
                        )}
                        MenuProps={MenuProps}
                        sx={{
                            '& .MuiSelect-select': {
                                display: 'flex',
                                flexWrap: 'wrap',
                                gap: 0.5,
                            }
                        }}
                    >
                        {genres.map((genre) => (
                            <MenuItem key={genre.id} value={genre.id}>
                                <Checkbox checked={selectedGenres.includes(genre.id)} />
                                <ListItemText primary={genre.name} />
                            </MenuItem>
                        ))}
                    </Select>
                </FormControl>
            </Box>

            {loading ? (
                <Box sx={{ display: 'flex', justifyContent: 'center', my: 8 }}>
                    <CircularProgress />
                </Box>
            ) : (
                <>
                    {filteredMovies.length === 0 ? (
                        <Typography variant="h6" sx={{ textAlign: 'center', my: 8 }}>
                            No movies found matching your filters.
                        </Typography>
                    ) : (
                        <>
                            <MovieGrid
                                movies={filteredMovies}
                                loading={loading}
                                genreMap={genreMap}
                            />

                            {totalPages > 1 && (
                                <Box sx={{ display: 'flex', justifyContent: 'center', mt: 6 }}>
                                    <Pagination
                                        count={totalPages}
                                        page={page}
                                        onChange={handlePageChange}
                                        color="primary"
                                        size="large"
                                        sx={{
                                            '& .MuiPaginationItem-root': {
                                                transition: 'all 0.3s ease',
                                                '&:hover': {
                                                    transform: 'translateY(-2px)',
                                                    backgroundColor: alpha(theme.palette.primary.main, 0.1)
                                                }
                                            }
                                        }}
                                    />
                                </Box>
                            )}
                        </>
                    )}
                </>
            )}
        </Container>
    );
};

export default MoviesPage; 