import React, { useState, useEffect } from 'react';
import { Box, Typography, Button, CircularProgress, Container } from '@mui/material';
import { Link } from 'react-router-dom';
import { Movie, Genre } from '../types';
import {
    getTrendingMovies,
    getTrendingTVShows,
    getTopRatedMovies,
    getTopRatedTVShows,
    getMovieGenres
} from '../services/movieService';
import Hero from '../components/Home/Hero';
import MovieGrid from '../components/Movies/MovieGrid';
import TrendingToggle from '../components/Movies/TrendingToggle';

const HomePage: React.FC = () => {
    const [trendingMovies, setTrendingMovies] = useState<Movie[]>([]);
    const [trendingTVShows, setTrendingTVShows] = useState<Movie[]>([]);
    const [topRatedMovies, setTopRatedMovies] = useState<Movie[]>([]);
    const [topRatedTVShows, setTopRatedTVShows] = useState<Movie[]>([]);
    const [genreMap, setGenreMap] = useState<Record<number, string>>({});
    const [loading, setLoading] = useState<boolean>(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                setLoading(true);

                // Fetch genres first to build the genre map
                const genres = await getMovieGenres();
                const genreMapping: Record<number, string> = {};
                genres.forEach((genre: { id: number, name: string }) => {
                    genreMapping[genre.id] = genre.name;
                });
                setGenreMap(genreMapping);

                // Fetch all media data in parallel
                const [moviesData, tvShowsData, topMoviesData, topTVData] = await Promise.all([
                    getTrendingMovies(),
                    getTrendingTVShows(),
                    getTopRatedMovies(),
                    getTopRatedTVShows()
                ]);

                setTrendingMovies(moviesData.results || []);
                setTrendingTVShows(tvShowsData.results || []);
                setTopRatedMovies(topMoviesData.results || []);
                setTopRatedTVShows(topTVData.results || []);
                setLoading(false);
            } catch (error) {
                console.error('Error fetching data:', error);
                setError('Failed to fetch data. Please try again later.');
                setLoading(false);
            }
        };

        fetchData();
    }, []);

    if (error) {
        return (
            <Box sx={{ textAlign: 'center', my: 8, py: 8 }}>
                <Typography variant="h5" color="error" gutterBottom>
                    {error}
                </Typography>
                <Button
                    variant="contained"
                    onClick={() => window.location.reload()}
                    sx={{ mt: 2 }}
                >
                    Retry
                </Button>
            </Box>
        );
    }

    // Only render the Hero when we have movies to display
    const heroMovies = !loading && trendingMovies.length > 0 ? trendingMovies.slice(0, 5) : [];

    return (
        <Container maxWidth="xl">
            {/* Hero Section */}
            {heroMovies.length > 0 && (
                <Hero movies={heroMovies} />
            )}

            {/* Trending Section with Toggle */}
            <TrendingToggle
                movies={trendingMovies}
                tvShows={trendingTVShows}
                loading={loading}
                genreMap={genreMap}
            />

            {/* Top Rated Movies Section */}
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h4" component="h2" fontWeight="bold">
                    Top Rated Movies
                </Typography>
                <Button
                    component={Link}
                    to="/top-rated"
                    variant="outlined"
                    sx={{ fontWeight: 'bold' }}
                >
                    View All
                </Button>
            </Box>
            <MovieGrid
                movies={topRatedMovies.slice(0, 8)}
                loading={loading}
                genreMap={genreMap}
            />

            {/* Top Rated TV Shows Section */}
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
                <Typography variant="h4" component="h2" fontWeight="bold">
                    Top Rated TV Shows
                </Typography>
                <Button
                    component={Link}
                    to="/top-rated-tv"
                    variant="outlined"
                    sx={{ fontWeight: 'bold' }}
                >
                    View All
                </Button>
            </Box>
            <MovieGrid
                movies={topRatedTVShows.slice(0, 8)}
                loading={loading}
                genreMap={genreMap}
            />
        </Container>
    );
};

export default HomePage; 