import React from 'react';
import {
    Box,
    Typography,
    Paper,
    Grid,
    LinearProgress,
    useTheme
} from '@mui/material';
import { styled } from '@mui/material/styles';

interface RatingDistribution {
    rating: number;
    count: number;
    percentage: number;
}

interface RatingsDistributionProps {
    distributions: RatingDistribution[];
    totalRatings: number;
}

const DistributionContainer = styled(Paper)(({ theme }) => ({
    padding: theme.spacing(3),
    marginBottom: theme.spacing(4),
    borderRadius: theme.shape.borderRadius,
}));

const RatingBar = styled(Box)(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    marginBottom: theme.spacing(1.5),
}));

const RatingsDistribution: React.FC<RatingsDistributionProps> = ({ distributions, totalRatings }) => {
    const theme = useTheme();

    // Sort distributions by rating (highest to lowest)
    const sortedDistributions = [...distributions].sort((a, b) => b.rating - a.rating);

    return (
        <DistributionContainer elevation={2}>
            <Typography variant="h6" gutterBottom>
                Ratings Distribution
            </Typography>
            <Typography variant="body2" color="text.secondary" gutterBottom>
                Based on {totalRatings} user ratings
            </Typography>

            <Box sx={{ mt: 3 }}>
                {sortedDistributions.map((dist, index) => (
                    <RatingBar key={index}>
                        <Typography variant="body2" sx={{ minWidth: 60 }}>
                            {dist.rating} stars
                        </Typography>
                        <Box sx={{ flexGrow: 1, mx: 2 }}>
                            <LinearProgress
                                variant="determinate"
                                value={dist.percentage}
                                sx={{
                                    height: 12,
                                    borderRadius: 6,
                                    backgroundColor: theme.palette.grey[200],
                                    '& .MuiLinearProgress-bar': {
                                        backgroundColor: theme.palette.primary.main,
                                    },
                                }}
                            />
                        </Box>
                        <Box sx={{ display: 'flex', alignItems: 'center', minWidth: 80 }}>
                            <Typography variant="body2" color="text.secondary">
                                {dist.count} ({Math.round(dist.percentage)}%)
                            </Typography>
                        </Box>
                    </RatingBar>
                ))}
            </Box>
        </DistributionContainer>
    );
};

export default RatingsDistribution; 